@import url(https://fonts.googleapis.com/css?family=Lato:100,100italic,300,300italic,regular,italic,700,700italic,900,900italic);

:root {
    --main-color: #029ed9;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

.wrapper {
    max-width: 100vw;
    position: relative;
    overflow: hidden;
}

.container {
    max-width: 1230px;
    padding: 0 15px;
    margin: 0 auto;
}

a {
    text-decoration: none;
    color: #000;
}

.toolbar {
    padding: 20px 0;
}

.toolbar__Body {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.toolbar__Logo {
    width: 55px;
    height: 55px;
    cursor: pointer;
    background-color: #fff;
    padding: 5px;
    border-radius: 10px;
}

.toolbar__Logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.toolbar__Menu {
    display: flex;
    align-items: center;
    column-gap: 40px;
    margin: 0;
    padding: 0;
}

.toolbar__Menu li {
    list-style: none;
}

.toolbar__Menu li a {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    color: #02012B;
}

.toolbar__Btn {
    background: var(--main-color);
    border-radius: 6px;
    padding: 12px 25px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}

.toolbar__Btn a {
    color: #FFFFFF;
}


.header {
    width: 100vw;
    background:
        linear-gradient(#0000005d, #0000005d),
        url('../img/header_Bc.jpg') no-repeat;
    background-position: center;
    background-size: cover;
}

.header__Body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 700px;
}

.header__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 76px;
    color: #FFFFFF;
    margin-bottom: 25px;
}

.header__Desc {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #FFFFFF;
    max-width: 620px;
    margin-bottom: 50px;
}

.header__Btn {
    background-color: var(--main-color);
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    padding: 20px 30px;
    border-radius: 10px;
}

.header__Btn a {
    color: #fff;
}



.aboutUs {
    margin: 100px 0;
}

.aboutUs__Body {
    display: flex;
    align-items: start;
    column-gap: 50px;
}

.aboutUs__Item {
    display: flex;
    flex-direction: column;
    align-items: start;
}

/* .aboutUs__PickBlock {
    width: 520px !important;
    height: 560px;
    object-fit: cover;
} */
.aboutUs__Img {
    width: 520px;
    max-width: 520px;
    height: 560px;
    object-fit: cover;
}

.aboutUs__subTitle {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: #02012B;
    margin-bottom: 10px;
}

.aboutUs__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 61px;
    color: #222222;
    margin-bottom: 20px;
}

.aboutUs__Desc {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: #555555;
    margin-bottom: 40px;
}

.aboutUs__Group {
    display: flex;
    align-items: start;
    column-gap: 40px;
    margin-bottom: 40px;
}

.aboutUs__Block {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
}

.aboutUs__Block img {
    width: 52px;
    height: 52px;
}

.aboutUs__BlockTitle {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #222222;
}

.aboutUs__BlockDesc {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #555555;

}

.aboutUs__Btn {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding: 15px 10px 15px 15px;
    border-radius: 6px;
    background-color: var(--main-color);
    display: flex;
    align-items: center;
    column-gap: 5px;
    color: #fff;
    cursor: pointer;
}

.aboutUs__Btn a {
    color: #FFFFFF;
}

.section1 {
    background-color: #02a0d93a;
    padding: 50px 0;
}

.section1__Body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section1__Group {
    display: flex;
    column-gap: 50px;
    align-items: center;
}

.section1__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 61px;
    color: #222222;
    margin-bottom: 50px;
    text-align: center;
}

.section1__Img {
    width: 520px;
    height: 560px;
}

.section1__Block {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

.section1__Item {
    background-color: #fff;
    display: flex;
    column-gap: 25px;
    padding: 20px 25px;
    border: 0.5px solid rgba(25, 188, 228, 0.2);
    border-radius: 5px;
    max-width: 570px;
    cursor: pointer;
}

.section1__Item:hover {}

.section1__ItemTitle {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #222222;
    margin-bottom: 5px;
}

.section1__ItemDesc {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #555555;
}


.section2 {
    margin: 50px 0;
}

.section2__Body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.section2__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 61px;
    color: #222222;
    margin-bottom: 10px;
}

.section2__Desc {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: #555555;
    margin-bottom: 25px;
}

.section2__Grid {
    display: grid;
    gap: 15px;
    grid-template-columns: 370px 370px 370px;
    grid-template-rows: 255px 255px;
}

.section2__Item {
    position: relative;
}

.section2__Item:nth-child(1) {
    grid-row-start: 1;
    grid-row-end: 3;
}

.section2__Item img {
    width: 100%;
    height: 100%;
}

.section2__Item span {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 10%;
    left: 50%;
    width: 200px;
    height: 88px;
    background: rgba(0, 96, 119, 0.02);
    backdrop-filter: blur(14.5px);
    transform: translateX(-50%);
    opacity: 0;
}
.section2__Item:hover .section2__Item::before{
    opacity: 1;
}
.whoUs {
    margin: 50px 0;
    
    padding: 50px 0;
}

.whoUs__Body {
    display: grid;
    grid-template-columns: calc(50% - 10px) calc(50% - 10px);
    align-items: center;
    justify-content: space-between;
    /* column-gap: 40px; */
}

.whoUs__Item {
    display: flex;
    flex-direction: column;
    align-items: start;
}

.whoUs__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 61px;
    color: #222222;
}

.whoUs__Desc {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: #555555;
    margin-bottom: 40px;
}

.whoUs__Btn {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
    border-radius: 6px;
    background-color: var(--main-color);
    padding: 15px 20px 15px 30px;
    display: flex;
    align-items: center;
    column-gap: 10px;
    cursor: pointer;
}

.whoUs__Img img {
    width: 600px;
    height: auto;
}

.reviews {
    background-color: #1b6c8914;
    border-top: 50px solid #4166740f;
    border-bottom: 50px solid #4166740f;
    margin: 0px 0 50px 0;
    padding: 50px 0;
}

.reviews__Body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.reviews__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 65px;
    color: #222222;
}

.reviews__Desc {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    color: #555555;
    margin-bottom: 50px;
}

.reviews__Group {
    display: grid;
    grid-template-columns: repeat(3, calc(33% - 20px));
    justify-content: space-between;
    margin-top: 60px;
}

.reviews__Item {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 15px;
    padding: 30px;
    cursor: pointer;
}

.reviews__ItemImg {
    margin-top: -100px;
}

.reviews__Name {
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #222222;
}

.reviews__Text {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #555555;
}

.contacts {
    background: linear-gradient(#00000099, #00000099), url(../img/contacUs_Bc.jpg) no-repeat center center/cover;
}

.contact-us {
    padding: 50px 0;
    background: linear-gradient(#00000099, #00000099), url(../img/contacUs_Bc.jpg) no-repeat center center/cover;
}

.contact-us__title {
    font-style: normal;
    font-weight: 600;
    font-size: 45px;
    line-height: 144.55%;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 5px;
    text-transform: uppercase;
}

.contact-us__subtitle {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 160%;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 35px;
}

.contact-us__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 20px;

}

.contact-us__form iframe {
    width: 90%;
    margin: 0 auto;
    height: 180px;
}

.contact-us__item {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    margin-bottom: 20px;
}

.contact-us__name {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 138%;
    color: #ffff;
}

.contact-us__input {
    background: #FFFFFF;
    opacity: 0.38;
    border: 1.5px solid #0A142F;
    border-radius: 5px;
    padding: 15px 25px;
}

.arua {
    height: 210px;
    display: flex;
    justify-content: center;
}

.contact-us__btn {
    background: #18396A;
    padding: 15px 40px;
    border: none;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 138%;
    text-align: center;
    color: #FFFFFF;
    border-radius: 5px;
    transition: .4s;
    cursor: pointer;
}


.textarea {
    height: 190px;
}

.textarea:focus {
    outline: 3px solid #5b73c4;
}

.contact-us__input:focus {
    outline: 3px solid #5b73c4;
}

.contact-us__inputs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    min-width: 600px;
    max-width: 700px;
}




.footer {
    padding: 50px;
}

.footer__Body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__Subtitle {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 2.8px;
    text-transform: uppercase;
    color: #0A142F;
    opacity: 0.6;
    margin-bottom: 20px;
}

.footer__Title {
    font-style: normal;
    font-weight: 700;
    font-size: 50px;
    line-height: 59px;
    text-align: center;
    color: #0A142F;
    margin-bottom: 20px;
}

.footer__data {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: #0A142F;
    margin-bottom: 40px;
}

.footer__End {
    border-top: 1px solid #029ed9;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 20px 0 0 0;
}

.footer__Logo {
    width: 55px;
    height: 55px;
    background-color: #fff;
    border-radius: 10px;
    padding: 5px;
    cursor: pointer;
}

.footer__Logo img {
    width: 100%;
    height: 100%;
}

.footer__Menu {
    display: flex;
    align-items: center;
    column-gap: 40px;
    margin: 0;
    padding: 0;
}

.footer__Menu li {
    list-style: none;
}

.footer__Group {
    display: flex;
    column-gap: 15px;
}